body {
	margin: 0;
	font-family: 'Segoe UI', 'Roboto', 'Oxygen', 'Helvetica Neue', 'Arial',
		'sans-serif';
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	color: #232323;
	/* font-size: large; */
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

/* BODY////////////////////////////////////////////////////////////








/////////////////////////////////////UTILITIES */
.btn {
	background-color: #ffbc0d;
	border-radius: 5px;
	font-weight: 500;
	padding: 1rem 1.5rem;
	text-decoration: none;
	text-align: center;
	text-overflow: ellipsis;
	color: #232323;
	z-index: 9;
}
.btn:hover {
	cursor: pointer;
}
.backDrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
	z-index: 11;
}
.backDrop img {
	display: block;
	max-width: 60%;
	max-height: 80%;
	margin: 60px auto;
	box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
}
/* UTILITIES//////////////////////////////////////////////////////////////////








///////////////////////////////////TOOLBAR */
.Toolbar {
	position: sticky;
	top: 0;
	height: 136px;
	width: 98.5vw;
	background-color: #fff;
	box-shadow: 0 0.0625rem 0.5rem 0 rgba(0, 0, 0, 0.04),
		0 0.0625rem 0.3125rem 0 rgba(0, 0, 0, 0.04);
	font-weight: 500;
	text-overflow: ellipsis;
	padding: 0 5rem;
	/* margin: 0 10%; EYES DO NOT ADJUST TO FIT WITH THIS OPTION*/
	z-index: 10;
}

.Link {
	color: #232323;
	text-decoration: none;
	text-align: center;
	flex: 20%;
}

#Bamhurger {
	display: none;
}

#Clown {
	margin-top: 22.3rem;
	opacity: 0;
	height: 130px;
}

.Logo {
	min-width: 100px;
	min-height: 100px;
	background: no-repeat center url(./assets/images/Toolbar/McDondalArch.png);
	background-size: contain;
}

.face {
	width: 30px;
	height: 30px;
	position: relative;
	left: 5.4rem;
	z-index: 1;
}

.eye {
	/* background: rgb(255, 220, 226); */
	width: 12px;
	height: 3px;
	border-radius: 50%;
	background: #fff;
	position: absolute;
	top: 1.55rem;
}

.left {
	left: -17px;
}

.right {
	right: 8px;
}

.pupil {
	width: 5px;
	height: 5px;
	background: #fff;
	border: solid 1px #232323;
	border-radius: 50%;
	position: absolute;
	left: calc(50% - 2.5px);
	bottom: calc(50% - 7px);
	z-index: 1;
}

.swell {
	bottom: calc(50% - 5px);
	animation: swell 0.5s;
}

@keyframes swell {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(2);
	}
	100% {
		transform: scale(1);
	}
}
/*TOOLBAR//////////////////////////////////////////////////////////////////









///////////////////////////////HOME*/
.Home {
	padding: 0 6rem 3rem 6rem;
	display: grid;
	grid-gap: 1rem;
	color: #232323;
	align-content: center;
	justify-content: center;
	align-items: center;
	justify-items: center;
	grid-template-columns: repeat(6, 1fr);
	grid-template-areas:
		'headline headline headline headline headline headline '
		'firstPic  firstPic firstPic  firstPic firstContent firstContent '
		'firstPic  firstPic  firstPic  firstPic firstContent firstContent'
		'firstPic  firstPic  firstPic  firstPic firstLink firstLink  '
		'secondContent secondContent secondPic secondPic secondPic secondPic '
		' secondContent secondContent secondPic secondPic secondPic secondPic'
		' secondLink secondLink secondPic secondPic secondPic secondPic';
}

.legal {
	font-size: 9px;
}

#headline {
	font-weight: 1000;
	font-size: 3.375rem;
	line-height: 1.2;
	text-align: center;
	grid-area: headline;
}

#firstPic {
	grid-area: firstPic;
	background-image: url(./assets/images/Home/Akiko1.jpg);
	background-size: cover;
	background-position: 25% 25%;
	width: 100%;
	height: 18rem;
}

#firstContent {
	grid-area: firstContent;
}

#firstLink {
	grid-area: firstLink;
}

#blank1 {
	grid-area: blank1;
}

#secondPic {
	grid-area: secondPic;
	background-image: url(./assets/images/Home/shitMyslef.jpg);
	background-size: cover;
	background-position: 25% 45%;
	width: 100%;
	height: 18rem;
}

#secondContent {
	text-align: right;
	grid-area: secondContent;
}

#blank2 {
	grid-area: blank2;
}

#secondLink {
	grid-area: secondLink;
}
/*HOME///////////////////////////////////////////////////////////////








///////////////////////////////MENU*/
.menu {
	padding: 2rem 6rem;
}
.menuScroller {
	width: 60rem;
	height: 20rem;
	margin: 1.5rem;
	border: 3.5px solid rgba(0, 0, 0, 0.04);
	border-radius: 2rem;
	background-color: rgba(0, 0, 0, 0.03);
}
.menuTitle {
	position: relative;
	width: 60rem;
	height: 1.7rem;
	padding: 0.5rem 0;
	text-align: center;
	border-radius: 2rem 2rem 0 0;
	background-color: #fff;
	box-shadow: 0 0.0625rem 0.5rem 0 rgba(0, 0, 0, 0.04),
		0 0.0625rem 0.3125rem 0 rgba(0, 0, 0, 0.04);
	z-index: 1;
}
.menuScrollFlex {
	width: inherit;
	height: 17rem;
	padding: 1rem 0;
	overflow-x: auto;
	overflow-y: hidden;
}

.menuItem {
	min-width: 14rem;
	height: 100%;
	margin: 0 1.2rem;
	/* animation: fadeIn ease 2s; */
	flex-grow: 1;
}

.menuItem img {
	width: 100%;
	height: 100%;
	border-radius: 1rem;
	object-fit: cover;
	transition: all 0.4s ease-in-out;
}

.menuItem img:hover {
	box-shadow: 0 0 2.5rem #232323;
	cursor: pointer;
}

.menuText {
	display: flex;
	flex-direction: column;
	background-color: #fff;
	margin: auto;
	width: 60%;
	height: 90%;
	border-radius: 2rem;
	text-align: center;
	overflow-y: auto;
}

.menuText div {
	margin: auto;
}

.menuText p {
	text-align: left;
	padding: 0 1rem;
}

.menuText .weaponsText {
	text-align: center;
}

.returnToMenu {
	position: relative;
	margin: auto;
	height: auto;
	width: 10rem;
	bottom: -0.5rem;
}

#mcBleach {
	padding-top: 24%;
}
#mcHydrogenPeroxide {
	padding-top: 30%;
}
#mcWaspSpray {
	padding-top: 32%;
}
#mcGas {
	padding-top: 25%;
}
