//////////////////////////////////ORDER */
#Order {
	display: flex;
	flex-direction: row;
}

.ChurgerFrame {
	transform: scale(1, 1);
	margin: auto;
	height: 640px;
	width: 640px;
}

.Churger {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin: auto;
	height: 100%;
	width: 100%;
}

.yellowCircleText {
	color: #fff;
	text-align: center;
}

