	color: #fff;
	font-size-adjust: 0.75;
}

.Ingredient {
	/* margin: -10.2rem 0; */
	margin: -25.3% 0;
	width: 45%;
	animation: ingredientDrop 0.18s;
}

@keyframes ingredientDrop {
	0% {
		transform: translateY(-100%);
	}
	75% {
		transform: translateY(25%);
	}
	100% {
		transform: translateY(0);
	}
}

