//////////////////////////////////HALP */
.Halp {
	margin: 3vh auto;
	border-bottom: none;
	border-radius: 12px 12px 0 0;
	box-shadow: 0.0625rem 0 0.7rem 0 rgba(0, 0, 0, 0.1),
		-0.0625rem 0 0.7rem 0 rgba(0, 0, 0, 0.1);
	background-color: rgba(0, 0, 0, 0.03);
	height: 100%;
	width: 1200px;
	overflow: hidden;
}

.Halp .ChatMain {
	height: 55vh;
	margin: 2vh 0;
	overflow-y: scroll;
}

.Halp form {
	height: 8.7vh;
	width: 90%;
	display: flex;
	font-size: 1.5rem;
	border-radius: 8px;
	margin-bottom: 1rem;
}

.Halp input {
	line-height: 1.5;
	width: 100%;
	font-size: 1.5rem;
	background: #fff;
	color: #232323;
	outline: none;
	border: none;
	padding: 0 15px;
	border-radius: 6px;
}

.Halp button {
	background-color: #ffbc0d;
	color: #232323;
	width: 20%;
	margin: 0 0 0 1rem;
	border: none;
	padding: 15px 32px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	cursor: pointer;
	font-size: 1.25rem;
}

.Halp button:disabled {
	cursor: not-allowed;
}

.Halp ul,
li {
	text-align: left;
	list-style: none;
}

.Halp p {
	max-width: 66%;
	line-height: 24px;
	padding: 10px 20px;
	border-radius: 25px;
	position: relative;
}

.Halp .Message {
	display: block;
	margin: 3.3rem 1.8rem;
}

.Halp .SentMessage p {
	background: #ea0602;
	color: #232323;
	text-align: right;
}
.Halp .ReceivedMessage p {
	background: #ffbc0d;
	text-align: left;
}
/*HALP///////////////////////////////////////////////////////////////








//////////////////////////////////FOOTER */
.Footer {
	padding: 1rem 0;
	box-shadow: 0 0.0625rem 0.5rem 0 rgba(0, 0, 0, 0.04),
		0 0.0625rem 0.3125rem 0 rgba(0, 0, 0, 0.04);
	width: 100%;
}

.Footer a {
	padding: 0.2rem;
	/* background-color: #ea0602; */
	background-color: #fff;
	color: #ea0602;
	border-radius: 4px;
	/* color: #232323; */
	font-weight: bold;
	/* text-shadow: 1px 1px #467bbd; */
	box-shadow: inset 2px 4px #ffbc0d;
}

.Footer > * {
	width: auto;
	margin: auto;
}
